<template>
  <div>
    <!-- 一条文章单元格 -->
    <van-cell>
      <!-- 标题区域的插槽 -->
      <template #title>
        <div class="title-box">
          <!-- 标题 -->
          <span>{{ artObj.title }}</span>
          <!-- 单图 -->
          <img
            v-if="artObj.cover.type === 1"
            class="thumb"
            v-lazy="artObj.cover.images[0]"
            alt=""
          />

          <!-- <van-image
            v-if="artObj.cover.type === 1"
            class="thumb"
            v-lazy="artObj.cover.images[0]"
          >
            <template v-slot:error>图片走丢了</template>
          </van-image> -->
        </div>
        <!-- 三张图片 -->
        <div v-if="artObj.cover.type > 1" class="thumb-box">
          <img
            v-for="(imgUrl, index) in artObj.cover.images"
            :key="index"
            class="thumb"
            v-lazy="imgUrl"
            alt=""
          />
          <!-- <van-image
            v-for="(imgUrl, index) in artObj.cover.images"
            :key="index"
            class="thumb"
            v-lazy="imgUrl"
          >
            <template v-slot:error>图片走丢了</template>
          </van-image> -->
        </div>
      </template>
      <!-- label 区域的插槽 -->
      <template #label>
        <div class="label-box">
          <div>
            <span>{{ artObj.aut_name }}</span>
            <span>{{ artObj.comm_count }}评论</span>
            <span>{{ formatTime(artObj.pubdate) }}</span>
          </div>
          <!-- 反馈按钮 -->
          <van-icon name="cross" @click.stop="show = true" v-show="isShow" />
        </div>
      </template>
    </van-cell>
    <!-- 反馈面板 -->
    <van-action-sheet
      v-model="show"
      :actions="actions"
      @select="onSelect"
      get-container="body"
      :cancel-text="bottomText"
      @cancel="cancelFn"
      @close="closeFn"
    />
  </div>
</template>

<script>
import { timeAgo } from '@/utils/date'
import { secondActions, firstActions } from '@/api/report'

export default {
  props: {
    artObj: { // 文章信息对象
      type: Object,
      default () {
        return {}
      }
    },
    isShow: {
      type: Boolean,
      default: true// 例如首页的地方我不想再去动了, 首页肯定没穿isShow值进来, 所以首页需要显示
    }
  },
  data () {
    return {
      show: false, // 反馈面板显示/隐藏
      actions: firstActions, // 反馈面板选项的数组（套对象）
      bottomText: '取消' // 底部按钮的文字
    }
  },
  methods: {
    formatTime: timeAgo, // formatTime就是一个函数
    // 反馈面板-获取选项选择事件
    onSelect (action, item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      console.log(action)// { name: '不感兴趣' }
      // console.log(item)// 索引值
      if (action.name === '反馈垃圾内容') {
        this.actions = secondActions
        this.bottomText = '返回'
      } else if (action.name === '不感兴趣') {
        this.$emit('disLikeEV', this.artObj.art_id)
        this.show = false
      } else {
        this.$emit('reportsEV', this.artObj.art_id, action.value)
        this.show = false
      }
    },
    // 反馈面板-底部按钮--点击取消按钮触发
    cancelFn () {
      if (this.bottomText === '返回') {
        this.actions = firstActions
        this.show = true
        this.bottomText = '取消'
      } else {
        this.show = false
      }
    },
    // 反馈面板-关闭面板触发事件
    closeFn () {
      this.actions = firstActions
      this.bottomText = '取消'
    }
  }
}
</script>

<style scoped lang="less">
/* 标题样式 */
.title-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

/* label描述样式 */
.label-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 文章信息span */
.label-box span {
  margin: 0 3px;
  &:first-child {
    margin-left: 0;
  }
}
/* 图片的样式, 矩形黄金比例：0.618 */
.thumb {
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}
/* 三图, 图片容器 */
.thumb-box {
  display: flex;
  justify-content: space-between;
}
</style>
